<template>
  <!-- 点评管理 -->
  <div class="management">
    <el-row :gutter="10" class="short-head">
      <el-col :span="19" class="stem" v-html="comment.stem" />
      <el-col :span="4" :offset="1" class="student-name">
        <div class="name">姓名：{{ comment.name }}</div>
        <div class="result">结果：/</div>
      </el-col>
    </el-row>
    <div v-if="comment.options || comment.answers">
      <h3 class="title">点评列表</h3>
      <div class="answer-information">
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">学员答案</el-col>
          <el-col :span="20">
            <p class="problem">该用户出现的问题罗列：</p>
            <p class="info" v-html="comment.options || '无'" />
            <p class="problem primary-problem">该用户主要问题：</p>
            <p class="info" v-html="comment.answers || '无'" />
          </el-col>
        </el-row>
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">正确答案</el-col>
          <el-col :span="20">
            <p v-html="comment.standard_answers || '无'" />
          </el-col>
        </el-row>
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">答案解析</el-col>
          <el-col :span="20">
            <p v-html="comment.answer_analysis || '无'" />
          </el-col>
        </el-row>
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">点评</el-col>
          <el-col :span="20">
            <el-input
              v-if="diagnosisEdit"
              v-model="comment.diagnosis_review"
              type="textarea"
              resize="none"
              :rows="4"
              placeholder="请输入点评内容"
              maxlength="500"
              show-word-limit
            />
            <el-col :span="20">
              <p v-if="!diagnosisEdit">
                {{ comment.diagnosis_review || '无' }}
              </p>
              <p class="edit-del">
                <el-button
                  v-if="!diagnosisEdit"
                  type="text"
                  @click.native="isDelShow('diagnosis_review')"
                  >删除</el-button
                >
                <el-button
                  v-if="!diagnosisEdit"
                  type="text"
                  @click.native="onDiagnosisClick"
                  >{{ diagnosisEdit ? '保存' : '编辑' }}</el-button
                >
                <el-button
                  v-if="diagnosisEdit"
                  type="primary"
                  @click.native="onDiagnosisClick"
                  >提交</el-button
                >
              </p>
            </el-col>
          </el-col>
        </el-row>
        <el-row v-if="false" :gutter="10">
          <el-col :offset="2">
            <el-button type="primary">提交</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
    <div
      v-if="
        comment.analysis_idea || comment.inquiry || comment.handler_suggestion
      "
    >
      <h3 class="title">问诊训练</h3>
      <div class="answer-information">
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">学员答案</el-col>
          <el-col :span="20">
            <p class="problem">分析思路：</p>
            <p class="info">{{ comment.analysis_idea || '无' }}</p>
            <p class="problem primary-problem">问诊：</p>
            <p class="info">{{ comment.inquiry || '无' }}</p>
            <p class="problem primary-problem">处理意见：</p>
            <p class="info">{{ comment.handler_suggestion || '无' }}</p>
          </el-col>
        </el-row>
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">正确答案</el-col>
          <el-col :span="20">
            <p v-html="comment.standard_options || '无'" />
          </el-col>
        </el-row>
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">答案解析</el-col>
          <el-col :span="20">
            <p v-html="comment.answer_analysis || '无'" />
          </el-col>
        </el-row>
        <el-row :gutter="10" class="answer">
          <el-col :span="2" class="answer-title">点评</el-col>
          <el-col :span="20">
            <el-input
              v-if="processEdit"
              v-model="comment.process_review"
              type="textarea"
              resize="none"
              :rows="4"
              show-word-limit
              placeholder="请输入点评内容"
              maxlength="500"
            />
            <el-col :span="20">
              <p v-if="!processEdit" v-html="comment.process_review || '无'" />
              <p class="edit-del">
                <el-button
                  v-if="!processEdit"
                  type="text"
                  @click.native="isDelShow('process_review')"
                  >删除</el-button
                >
                <el-button
                  v-if="!processEdit"
                  type="text"
                  @click.native="onProcessClick"
                  >{{ processEdit ? '保存' : '编辑' }}</el-button
                >
                <el-button
                  v-if="processEdit"
                  type="primary"
                  @click.native="onProcessClick"
                  >提交</el-button
                >
              </p>
            </el-col>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="switch">
      <div class="prev">
        <el-link
          :underline="false"
          :type="preType"
          :disabled="preDisabled"
          @click.native="preClick"
          >上一个</el-link
        >
      </div>
      <div class="next">
        <el-link
          :underline="false"
          :type="nextType"
          :disabled="nextDisabled"
          @click.native="nextClick"
          >下一个</el-link
        >
      </div>
    </div>
    <el-dialog title="提示" :visible.sync="isDel" width="30%" center>
      <span>删除后，该点评内容将不再保留，请确定是否删除？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isDel = false">取 消</el-button>
        <el-button type="primary" @click="onProcessDel">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { postReview, delReview, reviewList } from '@/api/learningAdmin'
export default {
  data() {
    return {
      processEdit: false,
      diagnosisEdit: false,
      preDisabled: true,
      nextDisabled: true,
      preType: 'info',
      nextType: 'info',
      index: 0,
      count: 0,
      list: [],
      isDel: false,
      type: '',
      comment: {
        stem: '',
        standard_options: '',
        standard_answers: '',
        options: '',
        answers: '',
        process_review: '',
        diagnosis_review: '',
        analysis_idea: '',
        handler_suggestion: '',
        inquiry: '',
        name: ''
      }
    }
  },
  created() {
    this.getReviewList()
  },
  methods: {
    preClick(e) {
      e.stopPropagation()
      if (this.index === 0 && this.count > 0) {
        return false
      }
      this.index = this.index - 1
      this.setData(this.list, this.index)
      if (this.index === 0 && this.count > 0) this.preDisabled = true
    },
    nextClick(e) {
      e.stopPropagation()
      if (this.count === this.index + 1) {
        return false
      }
      this.index = this.index + 1
      this.setData(this.list, this.index)
      if (this.count === this.index + 1) this.nextDisabled = true
    },
    async getReviewList() {
      const { data } = await reviewList({ pageSize: 0 })
      if (data) {
        this.list = data.list
        this.index = this.list.findIndex(
          v => String(v._id) === this.$route.params.id
        )
        this.count = data.count
        this.setData(data.list, this.index)
      }
    },
    setData(list, index) {
      this.comment = Object.assign({}, list[index])
      this.diagnosisEdit = !this.comment.diagnosis_review
      this.processEdit = !this.comment.process_review
      if (this.count > this.index + 1 && this.index > 0) {
        this.preDisabled = false
        this.preType = 'primary'
      }
      if (this.count > this.index + 1) {
        this.nextDisabled = false
        this.nextType = 'primary'
      }
    },
    async onDiagnosisClick() {
      if (this.diagnosisEdit) {
        const { data, code } = await postReview({
          id: this.comment._id,
          diagnosis_review: this.comment.diagnosis_review
        })
        if (code === 20000 && data.result) {
          this.diagnosisEdit = false
          this.$message({
            message: '数据提交成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: data.message,
            type: 'error'
          })
        }
        return false
      }
      this.diagnosisEdit = true
    },
    async onProcessClick() {
      if (this.processEdit) {
        const { data, code } = await postReview({
          id: this.comment._id,
          process_review: this.comment.process_review
        })
        if (code === 20000 && data.result) {
          this.$message({
            message: '数据提交成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: data.message,
            type: 'error'
          })
        }
        this.processEdit = false
        return false
      }
      this.processEdit = true
    },
    async onProcessDel(type) {
      const { data } = await delReview(this.comment._id, { type: this.type })
      if (data.result) {
        this.$message({
          message: '删除成功',
          type: 'success'
        })
        this.isDel = false
        if (this.type === 'process_review') this.comment.process_review = ''
        else this.comment.diagnosis_review = ''
      } else {
        this.$message({
          message: data.message,
          type: 'error'
        })
      }
    },
    isDelShow(type) {
      this.type = type
      this.isDel = true
    }
  }
}
</script>

<style scoped lang="scss" type="text/scss">
.management {
  .short-head {
    height: 120px;
    .stem {
      overflow-y: auto;
      padding: 20px !important;
      height: 120px;
      font-size: 14px;
      color: #576277;
      border: 1px solid #ebeef5;
      border-radius: 4px;
    }
    .student-name {
      font-size: 14px;
      text-align: center;
      color: #0085ff;
      .name,
      .result {
        height: 60px;
        line-height: 60px;
        border: 1px solid #ebeef5;
        border-radius: 4px;
        box-sizing: border-box;
      }
      .name {
        border-bottom: none;
      }
    }
  }
  .title {
    padding: 20px 0;
    font-size: 20px;
    font-weight: bold;
    color: #333;
  }
  .answer-information {
    padding: 20px;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    .answer {
      margin-bottom: 20px;
      .answer-title {
        font-size: 14px;
        font-weight: 500;
        color: #17233d;
      }
      .problem {
        font-size: 14px;
        font-weight: 600;
        color: #17233d;
        &.primary-problem {
          margin-top: 24px;
        }
      }
      .info {
        margin: 12px 0;
      }
      .edit-del {
        margin-top: 15px;
      }
    }
  }
  /deep/.el-button {
    padding: 6px 20px;
    height: 32px;
  }
  .switch {
    position: fixed;
    top: 50%;
    right: 50px;
    margin-top: -60px;
    width: 164px;
    height: 120px;
    text-align: center;
    background: #fff;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    box-shadow: 0 2px 12px 0 rgba(153, 153, 153, 0.3);
    .prev,
    .next {
      height: 60px;
      line-height: 60px;
      box-sizing: border-box;
    }
    .prev {
      border-bottom: 1px solid #ebeef5;
    }
  }
}
</style>
